<template>
  <div id="app">
    <header class="container-fluid clearfix">
      <div class="logo">
        <img alt="bbs logo" src="">
      </div>
      
      <div class="routeLink">
        <router-link to="/">首页</router-link>
        <router-link v-show="user" to="/publish">发布</router-link>
        <router-link v-show="user" to="/poefile">我</router-link>
        <a v-show="user" href="javascript:;" @click="logout">登出</a>
        <router-link v-show="!user" to="/login">登录</router-link>
        <router-link v-show="!user" to="/register">注册</router-link>
      </div>
    </header>
    
      
    <router-view></router-view>
  </div>

</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import VueRouter from "vue-router"
import home from "./components/home.vue"
import Register from "./components/register.vue"
import login from './components/login.vue'
import post from './components/post.vue'
import publish from './components/publish.vue'
import user from './components/user.vue'
import Vue from 'vue';


Vue.use(VueRouter)

const routes = [
    {path:"/",component:home},
    {path:"/register",component:Register},
    {path:"/login",component:login},
    {path:"/post",component:post},
    {path:"/publish",component:publish},
    {path:"/user",component:user},

  ]
const router = new VueRouter({
  routes
})

export default {
  name      : 'app',
  components: {
    home
  },
  router,
  data () {
    return {
      user:null
    }
  },
  method:{
    async logout(){
      this.user = null
      await this.$http.get("/logout")
      this.$router.push("/")
    }
  },
  async beforeCreate(){
    let user      = await this.$http.get("/api/userInfo")
    this.user = user.data
    console.log("beforecreated 运行")
  }

}
</script>

